}

/* Horizontal rules */
hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid #E5E5E5;
}

/* Emphasis */
small,
.small {
  font-size: 80%;
  font-weight: 400;
}

mark,
.mark {
  padding: 0.2em;
  background-color: #f8fc94;
}

/* Lists */
.list-unstyled {
  padding-left: 0;
  list-style: none;
}

.list-inline {
  padding-left: 0;
  list-style: none;
}

.list-inline-item {
  display: inline-block;
}

.list-inline-item:not(:last-child) {
  margin-right: 0.5rem;
}

blockquote {
  font-size: 1.9rem;
  quotes: "\201C""\201D""\2018""\2019";
}

blockquote:before {
  left: -6px;
  position: relative;
  color: #B8B7B7;
  content: open-quote;
  font-size: 4em;
  line-height: 0.1rem;
  margin-right: 0.9rem;
  vertical-align: -0.4em;
}

cite {
  display: block;
  font-size: 1rem;
  color: #B8B7B7;
}

cite:before {
  content: "\2014 \0020";
}

/* Images */
.img-full {
  max-width: 100%;
  height: auto;
}

/* Code */
code {
  font-size: 87.5%;
  color: rgba(237, 31, 68, 1);
  word-break: break-all;
}

a > code {
  color: inherit;
}

kbd {
  padding: 0.2rem 0.4rem;
  font-size: 87.5%;
  color: rgba(255, 255, 255, 1);
  background-color: rgba(50, 50, 50, 1);
  border-radius: 0.2rem;
}

kbd kbd {
  padding: 0;
  font-size: 100%;
  font-weight: 700;
}

pre {
  display: block;
  font-size: 87.5%;
  color: rgba(50, 50, 50, 1);
}

pre code {
  font-size: inherit;
  color: inherit;
  word-break: normal;
}


